	      title: 'Can a machine be the next Shakespeare?',
	      type: 'Talk',
	      description: 'Neural networks can achieve superhuman performance at many different tasks such as image recognition, natural language processing and playing video games. But how good are they at writing POETRY? What happens when you feed them images and ask them to return a poem? Will they become super smart and conquer the world, return half-assed shitty poetry or maybe define a new path within the genre of writing? Well, lets found out...'
