	      description: 'Data orchestration takes time. Different tools and services need to be learned/implemented. What if you could use serverless architecture to achieve your goals? This session will show you the use cases of Azure Functions for data movement. Combining triggers and bindings (of Azure Functions) with Azure Data Factory can be done easy. Not sure? Let me show you a real world example of how I implemented Azure Functions in my project.'
