	        bio: 'Linn er en utadvendt og entusiastisk utvikler som liker å løse problemer ved hjelp av teknologi. Hun erfaring både fra backendsystemer utviklet i Java, til webapplikasjoner utviklet ved hjelp av React, JavaScript og CSS.',
	        email: 'linvik@knowit.no',
