	        bio: "Steinar har en mastergrad i Teknisk Kybernetikk fra NTNU og ble uteksaminert i 2016 med en spesialisering innenfor 'Navigasjon og Fartøystyring'. Denne graden dreier seg hovedsaklig om design og implementasjon av autonome systemer, som foreksempel droner, marine fartøy og roboter. Gjennom studiet har software vært essensielt for å lykkes, og Steinar har opparbeidet seg god erfaring med programmering.",
	        email: 'steinar.kraugerud@knowit.no',
